<template>
  <div class="xdining-scan">
    <scan-code
      v-show="isScan"
      :camera="isScan ? 'rear' : 'off'"
      @backEvent="isScan = false"
      @decodeEvent="onDecode"
      @errorEvent="onError"></scan-code>
    <div v-show="!isScan" class="xscan-status">
      <div class="xscan-status-top" :class="{ error: !scanStatus }">
        <i class="xsst-icon"></i>
        <p class="xsst-title">{{ scanStatus ? '核销成功' : '核销失败' }}</p>
        <p class="xsst-desc" v-show="!scanStatus">{{ scanMessage }}</p>
      </div>
      <div class="xbutton-box">
        <a class="xbutton fill continue" @click="startScan()" v-if="!initError">继续扫码</a>
        <a class="xbutton back" @click="backInfo()">返回</a>
      </div>
    </div>
    <audio ref="audioS" src="./audio/scan-success-tip.mp3"></audio>
    <audio ref="audioE" src="./audio/scan-error-tip.mp3"></audio>
  </div>
</template>

<script>
import scanCode from '@/components/scanCode/scanCode';

export default {
  name: 'diningScan',
  components: { scanCode },
  data() {
    return {
      initError: false, // 初始化错误
      isScan: true, // 是否正在扫码
      scanStatus: false, // 扫码状态
      scanMessage: '请重新扫码', // 扫码状态提示语
      scanInfo: {}, // 扫码成功后的对象
      queryInfo: {}, // 请求参数
      writeOffSuccessPrompt: 1, // 核销成功提示 0-未启用 1-启用
      writeOffFailurePrompt: 1, // 核销失败提示 0-未启用 1-启用
    };
  },
  methods: {
    /**
     * 扫码初始化错误
     */
    onError(message) {
      this.initError = true;
      this.isScan = false;
      this.scanMessage = message;
    },
    /**
     * 后勤-餐饮
     * 用餐关系的核销接口 author：刘思远
     */
    onDecode(url) {
      if (Math.random() > 0.5) {
        this.scanStatus = true;
        this.scanMessage = '核销成功';
      } else {
        this.scanStatus = false;
        this.scanMessage = '核销失败';
      }

      if (this.scanStatus && this.writeOffSuccessPrompt) {
        window.alert('核销成功');
        this.playAudio();
      } else if (!this.scanStatus && this.writeOffFailurePrompt) {
        window.alert('核销失败');
        this.playAudio(false);
      } else {
        this.isScan = false;
      }
    },
    /**
     * 跳转回信息页
     */
    backInfo() {
      console.log('back');
    },
    /**
     * 开始扫码
     */
    startScan() {
      this.isScan = true;
    },
    /**
     * 播放音乐
     */
    playAudio(isSuccess) {
      const refName = isSuccess ? 'audioS' : 'audioE';

      this.$refs[refName].currentTime = 0;
      this.$refs[refName].play();
    },
  },
};
</script>

